<template>
  <div class="collection">
    <div
      id="instruction"
      style="cursor:default;position: fixed;right: 4px;top: 150px;width: 25px;height: 80px;line-height: 20px;text-align:center;color:#F8A6AA;border: solid 1px #F8A6AA; border-radius:3px;font-size: 14px"
    >
      <span style="width: 30px">功</span>
      <span style="width: 30px">能</span>
      <span style="width: 30px">说</span>
      <span style="width: 30px">明</span>
    </div>
    <div class="hot-notice">
      <img src="../../../static/img/message.png" alt style="width: 18px;height: 18px;margin-bottom: 4px"/>
      <span>1、该查询统计表仅能查询已申报成功的数据，不包含未申报、申报失败，已申报但已作废或者已被更正的数据</span>
      <div style="text-indent: 22px;">2、该表只能从2019年的数据开始查询，跨度不超过12个月，2019年之前的数据请切换到旧版进行查询</div>
      <div style="text-indent: 22px;">3、由于数据处理的滞后性，建议您在申报成功3天之后再来查询最新的申报数据</div>
    </div>
    <div class="rg-top-bottom">
      <el-button type="primary" size="small">导出</el-button>
      <el-button type="primary" size="small">收折查询条件</el-button>
    </div>
    <div class="select-person" style="padding:0 10px;">
      <div>
        <div>
          <span>税款所属期：</span>
          <el-date-picker type="date" placeholder style="width: 150px" value-format="yyyy-MM-dd"></el-date-picker> 至
          <el-date-picker type="date" placeholder style="width: 150px" value-format="yyyy-MM-dd"></el-date-picker>
        </div>
        <div style="margin-top: 15px">
          <span style="margin-left: 35px">姓名：</span> <el-input style="width: 150px"></el-input>
          <span>国籍/地区：</span> <el-input style="width: 150px"></el-input>
          <span>证件号码：</span> <el-input style="width: 150px"></el-input>
        </div>
      </div>
      <div style="width: 100%;text-align: right">
        <el-button size="small" type="primary">查询</el-button>
        <el-button size="small">重置</el-button>
      </div>
    </div>
    <div class="table-select">
      <div class=" con_btn2">汇总</div>
      <div class=" con_btn1">综合所得申报表</div>
      <div class=" con_btn2">分类所得申报表</div>
      <div class=" con_btn2">非居民所得申报表</div>
      <div class=" con_btn2">限售股权所得申报表</div>
    </div>
    <div style="margin: 0 10px;">
      <div id="con_in" class="con-div" style="overflow-y: auto; display: block;">
        <el-table :data="tableDataIn" border style="max-width:100%">
          <el-table-column label="税款所属期">
            <template>{{ company[0]['shenbao_month'] }}</template>
          </el-table-column>
          <el-table-column prop="income" label="本期收入"></el-table-column>
          <el-table-column prop="otherAll.jianchufeiyongbiaozhun" label="本期费用"></el-table-column>
          <el-table-column prop="incomeNoTax" label="本期免税收入"></el-table-column>
          <el-table-column prop="otherAll.jianchufeiyongbiaozhun" label="本期减除费用"></el-table-column>
          <el-table-column prop="yanglaobaoxian" label="本期专项扣除基本养老保险费"></el-table-column>
          <el-table-column prop="yiliaobaoxian" label="本期专项扣除基本医疗保险费"></el-table-column>
          <el-table-column prop="shiyebaoxian" label="本期专项扣除失业保险费"></el-table-column>
          <el-table-column prop="zhufanggongjijin" label="本期专项扣除住房公积金"></el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  components: {},
  data () {
    return {}
  },
  computed: {
    ...mapGetters(['company']),
    tableDataIn () {
      return this.$store.state.form.allPersonLog
    }
  },
  created () {
    this.$emit('router', this.$router.history.current.name)
  },
  mounted () {

  },
  methods: {}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  li {
    display: block;
    margin: 0;
  }

  img {
    vertical-align: middle;
  }

  .collection {
    font-size: 12px;
    text-align: start;
  }

  .hot-notice {
    border: solid 1px #d2e7fb;
    background-color: #eaf4ff;
    box-sizing: border-box;
    margin: 10px 35px 0 10px;
    padding: 8px;
    line-height: 20px;
    text-align: left;
  }

  .rg-top {
    border: solid 1px #e0e0e0;
    padding: 0 10px;
    margin: 0 35px 0 10px;
    font-size: 16px;
    font-weight: bold;
    color: #000000;
  }

  .rg-top-top {
    height: 40px;
    overflow: hidden;
    padding: 0 10px;
    line-height: 40px;
    box-sizing: border-box;
  }

  .rg-top-bottom {
    padding: 10px;
    line-height: 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: start;
  }

  .rg-top-bottom button {
    margin: 0 5px;
  }

  .right-btn {
    padding: 8px 10px 8px 10px;
    border: 1px #5a98de solid;
    border-radius: 5px;
    font-size: 14px;
  }

  .right-btn:hover {
    background-color: rgb(64, 158, 255);
    color: white;
  }

  .con_btn1 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid #4586f5;
    border-width: 0px 0px 2px 0px;
    font-size: 15px;
    color: #4586f5;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .con_btn2 {
    float: left;
    height: 33px;
    padding: 0 8px;
    margin: 0 3px;
    line-height: 33px;
    border: solid transparent;
    border-width: 0 0 2px 0;
    font-size: 15px;
    color: #000000;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .in-table .notice {
    display: block;
    width: 100%;
    height: 20px;
    /* display: block; */
    background-color: rgb(212, 244, 252);
  }

  .in-table-content {
    padding: 0 30px;
  }
</style>
<style>
</style>



